import { initState } from './state'
import { compileToFunction } from './compile/index'
export function initMixin(Vue) { //Ë°®Á§∫Âú®vueÁöÑÂü∫Á°Ä‰∏äÂÅö‰∏ÄÊ¨°Ê∑∑ÂêàÊìç‰Ωú
    Vue.prototype._init = function (options) {
        const vm = this;
        vm.$options = options;
        //ÂØπÊï∞ÊçÆËøõË°åÂàùÂßãÂåñ
        initState(vm) //vm.$options.data... Êï∞ÊçÆÂä´ÊåÅ


        if (vm.$options.el) {
            //Â∞ÜÊï∞ÊçÆÊåÇËΩΩÂà∞Ëøô‰∏™Ê®°Áâà‰∏ä
            vm.$mount(vm.$options.el)
        }
    }
    Vue.prototype.$mount = function (el) {
        const vm = this
        const options = vm.$options
        el = document.querySelector(el)
        //ÊääÊ®°ÁâàËΩ¨ÂåñÊàêÔºåÂØπÂ∫îÁöÑÊ∏≤ÊüìÂáΩÊï∞=„ÄãËôöÊãüdomÊ¶ÇÂøµ vnode =„ÄãdiffÁÆóÊ≥ï Êõ¥Êñ∞ËôöÊãüdom =„Äã‰∫ßÁîüÁúüÂÆûËäÇÁÇπÔºåÊõ¥Êñ∞
        if (!options.render) { //Ê≤°ÊúârenderÂáΩÊï∞ÔºåÁî®template
            let template = options.template
            if (!template && el) { //Áî®Êà∑‰πüÊ≤°Êúâ‰º†ÈÄítemplateÔºåÂ∞±Ëé∑ÂèñelÁöÑÂÜÖÂÆπ‰Ωú‰∏∫Ê®°Áâà
                template = el.outerHTML
                let render = compileToFunction(template) //ÊääÊ®°ÁâàÂèòÊàêÂáΩÊï∞
                options.render = render
            }
        }
    }
}