import { isFunction } from './util'
import { observer } from './observer/index'
export function initState(vm) { //Ë°®Á§∫Âú®vueÁöÑÂü∫Á°Ä‰∏äÂÅö‰∏ÄÊ¨°Ê∑∑ÂêàÊìç‰Ωú
    const opts = vm.$options
    if (opts.data) {
        initData(vm)
    }
    // if (opts.computed) {
    //     initComputed()
    // }
    // if (opts.watch) {
    //     initWatch()
    // }
}
function proxy(vm, source, key) {
    Object.defineProperty(vm, key, {
        get() {
            return vm[source][key]
        },
        set(newVal) {
            vm[source][key] = newVal
        }
    })
}
function initData(vm) { //vueÁöÑÂÜÖÈÉ®ÂèòÈáè‰ª•$ÂºÄÂ§¥Ôºå‰∏ç‰ºöËøõË°å‰ª£ÁêÜ
    let data = vm.$options.data
    //vue2 ‰ºöÂ∞Üdata‰∏≠ÂØπÊâÄÊúâÊï∞ÊçÆÔºåËøõË°åÊï∞ÊçÆÂä´ÊåÅ Object.defineProperty
    //Ëøô‰∏™Êó∂ÂÄôvmÂíådataÊ≤°ÊúâÂÖ≥Á≥ªÔºåÈÄöËøá_dataËøõË°åÂÖ≥ËÅî
    data = vm._data = isFunction(data) ? data.call(vm) : data;
    for (let key in data) { //vm.name =>am._data.name ‰ª£ÁêÜ
        proxy(vm, '_data', key)
    }
    observer(data)
}