//push shift pop unshift reverse sort splice,7‰∏™ÊñπÊ≥ïÊòØÂèòÂºÇÊñπÊ≥ïÔºå‰ºöÊõ¥ÊîπÂéüÊï∞ÁªÑ
let oldArrayPrototype = Array.prototype
export let arrayMethods = Object.create(oldArrayPrototype) //Êã∑Ë¥ùÂéüÊúâÁöÑÂéüÂûãÊñπÊ≥ï
//arrayMethods.__proto__ = Array.prototype ÁªßÊâø
let methods = [
    'push', 'shift', 'pop', 'unshift', 'reverse', 'sort', 'splice'
]
methods.forEach(method => {
    arrayMethods[method] = function (...args) {
        //console.log('Êï∞ÁªÑÂèëÁîüÂèòÂåñ,ÈáçÂÜôËøô7‰∏™ÊñπÊ≥ï')
        oldArrayPrototype[method].call(this, ...args)
        let inserted;
        let ob = this.__ob__
        switch (method) {
            case 'push':
            case 'unshift':
                inserted = args;
                break;
            case 'splice':
                inserted = args.slice(2);
                break;
            default:
                break;
        }
        //Â¶ÇÊûúÊúâÊñ∞Â¢ûÁöÑÂÜÖÂÆπË¶ÅÁªßÁª≠ËøõË°åÂä´ÊåÅÔºåÊàëÈúÄË¶ÅËßÇÊµãÁöÑÊï∞ÁªÑÈáåÁöÑÊØè‰∏ÄÈ°πÔºåËÄå‰∏çÊòØÊï∞ÁªÑÔºàÂ∑≤Êñ∞Â¢û‰∏∫‰æãÔºâ
        if (inserted) ob.observeArray(inserted)
    }
})