import { isObject } from '../util'
import { arrayMethods } from './array'

//Ê£ÄÊµãÊï∞ÊçÆÂèòÂåñÔºåÁ±ªÊúâÁ±ªÂûãÔºåÂØπË±°Êó†Á±ªÂûã
class Observer {
    constructor(data) { //ÂØπÂØπË±°‰∏≠ÁöÑÊâÄÊúâÂ±ûÊÄßËøõË°åÂä´ÊåÅ
        Object.defineProperty(data, "__ob__", {
            value: this,
            enumerable: false
        })
        //data.__ob__ = this  //‰ºö‰∏çÂÅúÈÄíÂΩíÔºåÁàÜÊ†àüí•
        //Áî®Êà∑ÂæàÂ∞ëÈÄöËøáÁ¥¢ÂºïÊï∞ÁªÑ arr[98]=100,ÊâÄ‰ª•ÂÜÖÈÉ®ÊÉ≥Âà∞‰∏çÂØπÁ¥¢ÂºïËøõË°åÊã¶Êà™ÔºåÂõ†‰∏∫Ê∂àËÄó‰∏•Èáç
        //ÁªèÂ∏∏‰ΩøÁî® push shift pop unshift reverse sort splice 7‰∏™ÂèòÂºÇÊñπÊ≥ïÔºåÂèØËÉΩ‰ºöÊõ¥ÊîπÂéüÊï∞ÁªÑ
        //Êï∞ÁªÑÊ≤°ÊúâÁõëÊéßÁ¥¢ÂºïÁöÑÂèòÂåñÔºå‰ΩÜÊòØÁ¥¢ÂºïÂØπÂ∫îÁöÑÂÜÖÂÆπÊòØÂØπË±°Á±ªÂûãÔºåÈúÄË¶ÅË¢´ÁõëÊéß Object.freeze
        if (Array.isArray(data)) { //Êï∞ÁªÑÂä´ÊåÅÁöÑÊã¶Êà™
            //ÂØπÊï∞ÁªÑÂéüÊù•ÁöÑÊñπÊ≥ïËøõË°åÊîπÂÜô,ÂàáÁâáÁºñÁ®ã È´òÈò∂ÂáΩÊï∞ÔºàÂåÖË£Ö‰∏ÄÂ±ÇÔºâ
            data.__proto__ = arrayMethods;
            //Â¶ÇÊûúÊï∞ÁªÑ‰∏≠ÁöÑÊï∞ÊçÆÊòØÂØπË±°Á±ªÂûãÔºåÈúÄË¶ÅÁõëÊéßÂØπË±°ÁöÑÂèòÂåñ
            this.observeArray(data)
        } else {
            this.walk(data) //ÂØπË±°Âä´ÊåÅÁöÑÊã¶Êà™
        }
    }
    observeArray(data) { //ÂØπÊï∞ÁªÑ‰∏≠ÁöÑÊï∞ÁªÑÂíåÂØπË±°ËøõË°åÂÜçÊ¨°Âä´ÊåÅÔºåÈÄíÂΩí
        data.forEach(item => {
            observer(item)
        })
    }
    walk(data) { //ÂØπË±°
        Object.keys(data).forEach(key => {
            defineReactive(data, key, data[key])
        })
    }
}
//vue2‰ºöÂØπÂØπË±°ËøõË°åÈÄíÂΩíÈÅçÂéÜÔºåÂ∞ÜÊØè‰∏™Â±ûÊÄßÁî® defineProperty ÈáçÊñ∞ÂÆö‰πâ ÊÄßËÉΩÂ∑Æ
function defineReactive(data, key, value) {
    observer(value) //ÂØπË±°Â∞ΩÂèØËÉΩÊâÅÂπ≥ÂåñÔºå‰∏çË¶ÅÂ§öÂ±ÇÈÄíÂΩí
    Object.defineProperty(data, key, {
        get() {
            return value;
        },
        set(newV) { //Â¶ÇÊûúÁî®Êà∑ËµãÂÄº‰∏Ä‰∏™Êñ∞ÂØπË±°ÔºåÈúÄË¶ÅÂ∞ÜËøô‰∏™ÂØπË±°ËøõË°åÂä´ÊåÅ
            observer(newV)
            value = newV
        }
    })
}
export function observer(data) {
    //Â¶ÇÊûúÊòØÂØπË±°ÊâçËßÇÊµã
    if (!isObject(data)) {
        return;
    }
    if (data.__ob__) {
        return;
    }
    return new Observer(data)
}