const ncname = '[a-zA-Z_][\\w\\-\\.]*'; // Ê†áÁ≠æÂêç <div></div>
const qnameCapture = `((?:${ncname}\\:)?${ncname})`; // Áî®Êù•Ëé∑ÂèñÊ†áÁ≠æÂêçÔºömatchÂêéÁ¥¢Âºï‰∏∫1ÁöÑ
const startTagOpen = new RegExp(`^<${qnameCapture}`); // ÂåπÈÖçÂºÄÂßãÊ†áÁ≠æ
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`); // ÂåπÈÖçÈó≠ÂêàÊ†áÁ≠æ
const attribute = /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/; // ÂåπÈÖçÂ±ûÊÄßÁöÑkey=value{"xx"| 'xx'| xx }
const startTagClose = /^\s*(\/?)>/; // /> </div>
const defaultRE = /\{\{((?:.|\r?\n))+?\}\}/g; // {{xxxx}}
//const doctype = /^<!DOCTYPE [^>]+>/i
//const comment = /^<!\--/
