//htmlÂ≠óÁ¨¶‰∏≤Ëß£ÊûêÊàêÂØπÂ∫îÁöÑËÑöÊú¨Êù•Ëß¶Âèë tokens <div id="app">{{name}}</div>
function parserHTML(html) {
    function advance(len) {
        html = html.substring(len)
    }
    function parserStartTag() {
        const start = html.match(startTagOpen)
        if (start) {
            const match = {
                tagName: start[1], //Ê†áÁ≠æÂêç
                attrs: []
            }
            advance(start[0].length)
            let end; //Â¶ÇÊûúÊ≤°ÊúâÈÅáÂà∞Ê†áÁ≠æÁªìÂ∞æÂ∞±‰ºö‰∏çÂÅúÁöÑËß£Êûê
            let attr;
            while (!(end = html.match(startTagClose)) && (attr = html.match(attribute))) {
                match.attrs.push({ name: attr[1], value: attr[3] || attr[4] || attr[5] })
                advance(attr[0].length)
            }
            if (end) {
                advance(end.length)
            }
        }
        return false; //‰∏çÊòØÂºÄÂßãÊ†áÁ≠æ
    }
    while (html) { //Ë¶ÅËß£ÊûêÁöÑÂÜÖÂÆπÊòØÂê¶Â≠òÂú®
        let textEnd = html.indexOf('<')
        if (textEnd == 0) {
            const startTagMatch = parserStartTag(html) //Ëß£ÊûêÂºÄÂßãÊ†áÁ≠æ
            if (startTagMatch) {
                start(startTagMatch.tagName, startTagMatch.attrs)
                continue;
            }
            break;
            // const endTagMatch = parserEndTag() //Ëß£ÊûêÂºÄÂßãÊ†áÁ≠æ
            // if (endTagMatch) {

            // }
        }
        let text; //  {{name}}</div>
        if (textEnd > 0) {
            text = html.substring(0, textEnd)
        }
        if(text){
            chars(text)
            advance(text.length)
        }

    }
}


export const compileToFunction = function (template) {
    //Â∫ì htmlparser2
    parserHTML(template)
}
